import React from 'react';
import { Input, Form } from 'antd';

const dummy = {
  isLoggedIn : true,
}

const Home = () => {
  return (
    <>
      {dummy.isLoggedIn && 
        <Form encType="multipart/form-data">
          <Input.TextArea maxLength={140} placeholder="어떤 신기한 일이 있었나요?" />
        </Form>
      }
    </>
  );
}

export default Home;